import React from 'react';
import { BrowserRouter as Router, Switch, Route, useParams } from 'react-router-dom'
import { Search } from './pages'

const ShopDetail = () => {
  const { id }: any = useParams()

  return (
    <p>{id}</p>
  )
}

const App = () => (
  <Router> 
    <Switch>
      <Route exact path="/halfhalf/search" component={Search} />
      <Route exact path="/halfhalf/shop/:id" component={ShopDetail} />
    </Switch>
  </Router>
)

export default App